@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}


@mixin button($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;

    @include tablet {
        width: auto;
    }

    &:hover {
        background-color: darken($bgColor, 10%);
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin heading($br: false) {
    font-weight: 400;

    span {
        font-weight: 700;

        @if($br ==true) { // $br es el salto
            display: block; // Para que haya un salto de línea
        }
    }

    &::after {
        content: '';
        display: block;
        width: 15rem;
        height: 1rem;
        margin-top: 1rem;
        background-image: linear-gradient(to right, v.$primary 0%, v.$primary 50%, lighten(v.$primary, 50%) 50%, lighten(v.$primary, 50%) 100%);
    }
}